import { MultiStopwatchUtils, useStopwatchManager } from 'utils';
import React, { useState } from 'react';
import { Center, Heading, HStack, IconButton, VStack } from '@chakra-ui/react';
import { AddIcon, CheckIcon, EditIcon } from '@chakra-ui/icons';
import MultiStopwatchLink from './multiStopwatchLink';
import EditMultiStopwatch from './editMultiStopwatch';

export default () => {
  const { stopwatchIds: stopwatches, removeStopwatch, addStopwatch } = useStopwatchManager();
  const [editing, setEditing] = useState<boolean>(false);

  return (
    <VStack align={'flex-start'}>
      <Heading>Stopwatch Manager</Heading>

      {stopwatches.map((id, index) =>
        editing ? (
          <EditMultiStopwatch key={index} id={id} remove={() => removeStopwatch(id)} />
        ) : (
          <MultiStopwatchLink key={index} id={id} />
        ),
      )}

      <Center>
        <HStack>
          <IconButton
            aria-label={'Edit MultiStopwatch'}
            onClick={() => setEditing(!editing)}
            icon={editing ? <CheckIcon /> : <EditIcon />}
          />
          <IconButton aria-label={'Add Stopwatch'} onClick={addStopwatch} icon={<AddIcon />} />
        </HStack>
      </Center>
    </VStack>
  );
};
