import { useMultiStopwatch } from 'utils';
import { HStack, IconButton, Input } from '@chakra-ui/react';
import { CloseIcon } from '@chakra-ui/icons';
import React from 'react';

export default ({ id, remove }: { id: string; remove: () => void }) => {
  const { multiStopwatch, setName } = useMultiStopwatch(id);

  return (
    <HStack>
      <Input value={multiStopwatch.name} onChange={e => setName(e.target.value)} />
      <IconButton
        colorScheme={'red'}
        aria-label={'Remove Stopwatch'}
        onClick={remove}
        icon={<CloseIcon />}
      />
    </HStack>
  );
};
